import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-invoices',
  templateUrl: './invoices.component.html',
  styleUrls: ['./invoices.component.scss']
})
export class InvoicesComponent implements OnInit {

  constructor(
    private router: Router,
    private vhAuth: VhAuth,
    public languageService: LanguageService,
    public vhComponent: VhComponent,
  ) { }

  ngOnInit(): void {
  }

  gotoPurchaseReturned() {
    this.router.navigate(['/sales/dashboard/manage/invoice/purchase-return-invoice']);
  }

  gotoSalesReturned() {
    this.router.navigate(['/sales/dashboard/manage/invoice/sales-return-invoice']);
  }
  gotoWarranty() {
    this.router.navigate(['/sales/dashboard/manage/invoice/warranty'])
  }
  gotoPurchase() {
    if (this.vhAuth.checkMyPermission("purchase_enable_view_bill_today"))
      this.router.navigate(['/sales/dashboard/manage/invoice/purchase']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoSupplierReturn() {
    if (this.vhAuth.checkMyPermission("return_enable_supplier")) this.router.navigate(['/sales/dashboard/manage/invoice/supplier-return']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoCustomerReturn() {
    if (this.vhAuth.checkMyPermission("return_enable_customer")) this.router.navigate(['/sales/dashboard/manage/invoice/customer-return']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoOpeningStock() {
    if (this.vhAuth.checkMyPermission("import_enable_opening_stock")) this.router.navigate(['/sales/dashboard/manage/invoice/opening-stock']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoTemporary() {
    this.router.navigate(['/sales/dashboard/manage/invoice/temporary-sales-invoice']);
  }
  gotoTemporaryPurchase() {
    this.router.navigate(['/sales/dashboard/manage/invoice/temporary-purchase-invoice']);
  }
  
  gotoDestroyProduct() {
    this.router.navigate(['/sales/dashboard/manage/invoice/destroy-product']);
  }

  gotoInvoiceMember() {
    this.router.navigate(['/sales/dashboard/manage/invoice/sales-return-customer-invoice']);
  }

  gotoInvoiceRetail() {
    this.router.navigate(['/sales/dashboard/manage/invoice/sales-return-retail-invoice']);
  }

  gotoInvoiceSupplier() {
    this.router.navigate(['/sales/dashboard/manage/invoice/sales-return-supplier-invoice']);
  }

  gotoInvoiceEInvoice() {
    this.router.navigate(['/sales/dashboard/manage/invoice/e-invoice']);
  }
}
