import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { InvoicesComponent } from './invoices.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
const routes: Routes = [
  {
    path: '',
    component: InvoicesComponent
  },
  {
    path: 'purchase-return-invoice',
    loadChildren: () => import('./purchase-return-invoice/purchase-return-invoice.module').then(m => m.PurchaseReturnInvoiceModule)
  },
  {
    path: 'sales-return-invoice',
    loadChildren: () => import('./sales-return-invoice/sales-return-invoice.module').then(m => m.SalesReturnInvoiceModule)
  },
  {
    path: 'purchase',
    loadChildren: () => import('./purchase/purchase.module').then(m => m.PurchaseModule)
  },
  {
    path: 'supplier-return',
    loadChildren: () => import('./supplier-return/supplier-return.module').then(m => m.SupplierReturnModule)
  },
  {
    path: 'customer-return',
    loadChildren: () => import('./customer-return/customer-return.module').then(m => m.CustomerReturnModule)
  }, {
    path: 'opening-stock',
    loadChildren: () => import('./opening-stock/opening-stock.module').then(m => m.OpeningStockModule)
  },
  {
    path: 'warranty',
    loadChildren: () => import('./warranty-invoice/warranty-invoice.module').then(m => m.WarrantyInvoiceModule)
  },
  {
    path: 'temporary-sales-invoice',
    loadChildren: () => import('./temporary-sales-invoice/temporary-sales-invoice.module').then(m => m.TemporarySalesInvoiceModule)
  },
  {
    path: 'temporary-purchase-invoice',
    loadChildren: () => import('./temporary-purchase-invoice/temporary-purchase-invoice.module').then(m => m.TemporaryPurchaseInvoiceModule)
  },
  {
    path: 'destroy-product',
    loadChildren: () => import('./destroy-product/destroy-product.module').then(m => m.DestroyProductModule)
  },
  
  {
    path: 'sales-return-customer-invoice',
    loadChildren: () => import('./sales-return-customer-invoice/sales-return-customer-invoice.module').then(m => m.SalesReturnCustomerInvoiceModule)
  }, {
    path: 'sales-return-retail-invoice',
    loadChildren: () => import('./sales-return-retail-invoice/sales-return-retail-invoice.module').then(m => m.SalesReturnRetailInvoiceModule)
  },
  {
    path: 'sales-return-supplier-invoice',
    loadChildren: () => import('./sales-return-supplier-invoice/sales-return-supplier-invoice.module').then(m => m.SalesReturnSupplierInvoiceModule)
  },
  {
    path: 'e-invoice',
    loadChildren: () => import('./e-invoice/e-invoice.module').then(m => m.EInvoiceModule)
  },
]

@NgModule({
  declarations: [InvoicesComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    TranslateModule,
    ReactiveFormsModule
  ]
})
export class InvoicesModule { }
